$primary-color: #cde6f5;
$secondary-color: #87919e;
$font-family: "Cabin Condensed", sans-serif;

body {
  font-family: "Cabin Condensed", sans-serif;
}

p {
  margin: 0 0 16px;
}

h4 {
  margin: 0 0 8px;
}

.genre-view-card {
  background-color: $primary-color;
}

.genre-view {
  background-color: $primary-color;
  margin-top: 50px;
  margin-bottom: 50px;
  width: 100%;
  padding: 40px;
}

.back-from-genre:hover {
  background-color: #8c8c8c;
}

.back-from-genre {
  margin-top: auto;
  color: white;
  background-color: $secondary-color;
  border: 0;
  border-radius: 4px;
  padding: 10px;
  margin-left: 2px;
  border-radius: 5px;
}
